// import router from '../router';
import Vue from 'vue';
import HTTP from '../http';

export default {
  namespaced: true,
  state: {
    tareas: [],
    newTaskName: null,
  },
  actions: {
    saveTask({ commit }, tarea) {
      return HTTP().patch(`tareas/${tarea.id}`, tarea)
        .then(() => {
          commit('unsetEditMode', tarea);
        });
    },
    deleteTask({ commit }, tarea) {
      return HTTP().delete(`tareas/${tarea.id}`)
        .then(() => {
          commit('removeTask', tarea);
        });
    },

    fetchTasksForProject({ commit }, proyecto) {
      return HTTP().get(`proyectos/${proyecto.id}/tareas`)
        .then(({ data }) => {
          commit('setTasks', data);
        });
    },
    createTask({ commit, state, rootState }) {
      return HTTP().post(`proyectos/${rootState.proyectos.currentProject.id}/tareas`, {
        tarea: state.newTaskName,
      })
        .then(({ data }) => {
          commit('appendTask', data);
          commit('setNewTaskName', null);
        });
    },
  },
  getters: {
  },
  mutations: {
    setTasks(state, tareas) {
      state.tareas = tareas;
    },
    setNewTaskName(state, newTaskName) {
      state.newTaskName = newTaskName;
    },
    appendTask(state, tarea) {
      state.tareas.push(tarea);
    },
    setTaskDescription(state, { tarea, descripcion }) {
      tarea.tarea = descripcion;
    },
    setEditMode(state, tarea) {
      Vue.set(tarea, 'isEditMode', true);
    },
    unsetEditMode(state, tarea) {
      Vue.set(tarea, 'isEditMode', false);
    },
    removeTask(state, tarea) {
      state.tareas.splice(state.tareas.indexOf(tarea), 1);
    },
    toggleCompleted(state, tarea) {
      tarea.completado = !tarea.completado;
    },
  },
};
