import Vue from 'vue';
import HTTP from '../http';

export default {
  namespaced: true,
  state: {
    projects: [],
    currentProject: null,
    newProjectName: null,
  },
  actions: {
    saveProject({ commit }, proyecto) {
      return HTTP().patch(`proyectos/${proyecto.id}`, proyecto)
        .then(() => {
          commit('unsetEditMode', proyecto);
        });
    },
    deleteProject({ commit }, proyecto) {
      return HTTP().delete(`proyectos/${proyecto.id}`)
        .then(() => {
          commit('removeProject', proyecto);
        });
    },
    fetchProjects({ commit }) {
      return HTTP().get('/proyectos')
        .then(({ data }) => {
          commit('setProjects', data);
        });
    },
    createProject({ commit, state }) {
      return HTTP().post('/proyectos', {
        proyecto: state.newProjectName,
      })
        .then(({ data }) => {
          commit('appendProject', data);
          commit('setNewProjectName', null);
        });
    },
  },
  getters: {

  },
  mutations: {
    setCurrentProject(state, proyecto) {
      state.currentProject = proyecto;
    },
    setNewProjectName(state, name) {
      state.newProjectName = name;
    },
    appendProject(state, proyecto) {
      state.projects.push(proyecto);
    },
    setProjects(state, projects) {
      state.projects = projects;
    },
    setProjectTitle(state, { proyecto, titulo }) {
      proyecto.proyecto = titulo;
    },
    setEditMode(state, proyecto) {
      Vue.set(proyecto, 'isEditMode', true);
    },
    unsetEditMode(state, proyecto) {
      Vue.set(proyecto, 'isEditMode', false);
    },
    removeProject(state, proyecto) {
      state.projects.splice(state.projects.indexOf(proyecto), 1);
    },
  },
};
