<template>
  <Panel title="Tareas">
       <div
      class="task mt-2"
      v-for="tarea in tareas"
      :key="tarea.id"

      >
      <EditableRecord
     :isEditMode="tarea.isEditMode"
     :proyecto="tarea.tarea"
      @onInput="setTaskDescription({
       tarea,
       descripcion: $event,
     })"
     @onEdit="setEditMode(tarea)"
     @onSave="saveTask(tarea)"
     @onDelete="deleteTask(tarea)"
     >
      <v-icon
        @click="checkClicked(tarea)">
        {{ tarea.completado ? 'check_box' : 'check_box_outline_blank'}}
        </v-icon>
     </EditableRecord>
       </div>
      <CreateRecord
      placeholder="Agregar tarea"
      @onInput="setNewTaskName"
      :value="newTaskName"
      @create="createTask"
      />
  </Panel>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex';
import CreateRecord from '@/components/CreateRecord.vue';
import EditableRecord from '@/components/EditableRecord.vue';

export default {
  components: {
    CreateRecord,
    EditableRecord,
  },
  computed: {
    ...mapState('tareas', [
      'tareas',
      'newTaskName',
    ]),
  },
  methods: {
    ...mapActions('tareas', [
      'createTask',
      'deleteTask',
      'saveTask',
    ]),
    ...mapMutations('tareas', [
      'setNewTaskName',
      'setTaskDescription',
      'setEditMode',
      'toggleCompleted',
    ]),
    checkClicked(tarea) {
      this.toggleCompleted(tarea);
      this.saveTask(tarea);
    },
  },
};
</script>

