<template>
    <Panel title="Proyectos" >
      <div
      class="proyect mt-2"
      v-for="proyecto in projects"
      :key="proyecto.id"
      >
     <EditableRecord
     :isEditMode="proyecto.isEditMode"
     :proyecto="proyecto.proyecto"
      @onInput="setProjectTitle({
       proyecto,
       titulo: $event,
     })"
     @onClick="projectClicked(proyecto)"
     @onEdit="setEditMode(proyecto)"
     @onSave="saveProject(proyecto)"
     @onDelete="deleteProject(proyecto)"
     />
      </div>
      <CreateRecord
      placeholder="Nombre del Proyecto"
      @onInput="setNewProjectName"
      :value="newProjectName"
      @create="createProject"
      />
    </Panel>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex';
import CreateRecord from '@/components/CreateRecord.vue';
import EditableRecord from '@/components/EditableRecord.vue';

export default {
  mounted() {
    this.fetchProjects();
  },
  components: {
    CreateRecord,
    EditableRecord,
  },
  computed: {
    ...mapState('proyectos', [
      'newProjectName',
      'projects',
    ]),
  },
  methods: {
    projectClicked(proyecto) {
      this.fetchTasksForProject(proyecto);
      this.setCurrentProject(proyecto);
    },
    ...mapMutations('proyectos', [
      'setNewProjectName',
      'setEditMode',
      'setProjectTitle',
      'setCurrentProject',
    ]),
    ...mapActions('proyectos', [
      'createProject',
      'fetchProjects',
      'saveProject',
      'deleteProject',
    ]),
    ...mapActions('tareas', [
      'fetchTasksForProject',

    ]),
  },
};
</script>

<style>
.project {
  font-size: 24px;
}

.icon {
  cursor: pointer;
}

.icon:hover {
  color: #333;
}

</style>
